.welcome{
  display: flex;
  justify-content: center;
  margin-top: 350px !important;
  font-size: 70px;
  margin-top: 250px !important;
  margin-right: 0;
  margin-left: 0;
  padding-right: 0;
  text-align: center;
  font-family: Copperplate, Papyrus, fantasy;
  color: white;
}
.welbutton{
  display: flex;
  justify-content: center;
  background-color: white;
  font-family: Copperplate, Papyrus;
  border-radius: 20%;
  border : 10% solid rgb(77, 57, 189);
  width: 20%;
  height: 20%;
  margin-top: 120px !important;
  margin-left: 2%;
  line-height: 120%;
  font-size: 30px;
  cursor : pointer;
  color: rgb(114, 19, 170);
}

.logo {
  display: flex;
  justify-content: center;

  font-size: 50px;
  font-family: Copperplate, Papyrus, fantasy;
  color: white;
}

.logo h1 {
  margin-top: 20px !important;
  margin-right: 0;
  padding-right: 0;
  font-family: Copperplate, Papyrus, fantasy;
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: 0.15em solid #fefae0; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.1em; /* Adjust as needed */
  animation: typing 2s steps(10, end), blink-caret 0.75s step-end infinite;
  text-shadow: -1px -1px white, 1px 1px gray, 2px 2px #7a7a7a, 3px 3px #757575, 4px 4px #707070, 5px 5px #6b6b6b, 6px 6px #666666, 7px 7px #616161, 8px 8px #5c5c5c, 9px 9px #575757, 10px 10px #525252, 11px 11px #4d4d4d, 18px 18px 30px rgba(0, 0, 0, 0.4), 18px 18px 10px rgba(0, 0, 0, 0.4);
}

/* The typing effect */
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 375px;
  }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from,
  to {
    border-color: #fefae0;
  }
  50% {
    border-color: transparent;
  }
}
