*{
  font-family: fantasy
}

form{
  width:80% !important
}

.content {
  margin-bottom: 100px;

  padding: 40px !important;
  margin-top: 100px;
  left: 0px;
  border-radius: 20px;
  background-color: rgb(235, 232, 250) !important;
  color: white;
  position: absolute !important;
  z-index: 9999;
  opacity: 0;
  width: 15%;
  height: 10%;
  font-size: 15px;
  transition: 2s;
  overflow: hidden;
  transform-style: preserve-3d !important;
  transform: rotateX(80deg) translateZ(-120px);
  cursor: default !important;
  box-shadow: -1px -1px white, 1px 1px gray, 2px 2px #7a7a7a, 3px 3px #757575, 4px 4px #707070, 5px 5px #6b6b6b, 6px 6px #666666, 7px 7px #616161, 8px 8px #5c5c5c, 9px 9px #575757, 10px 10px #525252, 11px 11px #4d4d4d, 18px 18px 30px rgba(0, 0, 0, 0.4), 18px 18px 10px rgba(0, 0, 0, 0.4);
}

h1 {
  text-align: center;
  color: rgb(48, 30, 138);
  transition: 0.3s;
  text-shadow: white 0px 0px 5px;
}

h1:hover {
  text-shadow: 0 1px 0 rgb(178, 168, 230), 0 2px 0 rgb(178, 168, 230), 0 3px 0 rgb(178, 168, 230), 0 4px 0 rgb(178, 168, 230),
    0 5px 0 rgb(178, 168, 230), 0 6px 0 rgb(178, 168, 230), 0 7px 0 rgb(178, 168, 230), 0 8px 0 rgb(178, 168, 230), 0 9px 0 rgb(178, 168, 230),
    0 10px 0 rgb(178, 168, 230), 0 11px 0 rgb(178, 168, 230), 0 12px 0 rgb(178, 168, 230), 0 20px 30px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}
h2 {
  text-align: center;
  color: rgb(48, 30, 138);
  transition: 0.3s;
  text-shadow: white 0px 0px 5px;
}

h2:hover {
  text-shadow: 0 1px 0 rgb(178, 168, 230), 0 2px 0 rgb(178, 168, 230), 0 3px 0 rgb(178, 168, 230), 0 4px 0 rgb(178, 168, 230),
    0 5px 0 rgb(178, 168, 230), 0 6px 0 rgb(178, 168, 230), 0 7px 0 rgb(178, 168, 230), 0 8px 0 rgb(178, 168, 230), 0 9px 0 rgb(178, 168, 230),
    0 10px 0 rgb(178, 168, 230), 0 11px 0 rgb(178, 168, 230), 0 12px 0 rgb(178, 168, 230), 0 20px 30px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

h4 {
  text-align: center;
  color: rgb(48, 30, 138);
  transition: 0.3s;
  font-family: fantasy
}

#askFormWrapper {

  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  z-index: -1;
}

.contentForm{
  color: green;
  display:flex;
  flex-direction:column;
}

.contentButton:hover{
  background-color: rgb(178, 168, 230);
}

.contentInput{

  border-radius: 15px;
  border : 1px solid rgb(28, 18, 82);
  margin-top: 7px;
  line-height: 2;
  width: 100% ;
  font-size: 20px;

}


.contentInput:focus ~ span {
  transform: scaleX(1) !important;
  transform-origin: left center !important;
}

.contentButton{
  box-shadow: 0px 0px 10px 0px rgb(20, 12, 64);
  margin-left: 10px;
  border-radius: 10px;
  border : 1px solid rgb(35, 19, 125);
  margin-top: 7px;
  line-height: 1.5;
  font-size: 20px;
  cursor : pointer;
}

.contentHr {
  margin-top: 50px;
  margin-bottom: 30px;
  width: 100%;
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.questions{
  text-align: center;
  color: green;
  transition: 0.3s;
  font-family: Arial, Helvetica, sans-serif;
  text-shadow: black 0px 0px 5px;

}
.answers{
  text-align: center;
  color: orange;
  transition: 0.3s;
  font-family: Arial, Helvetica, sans-serif;
  text-shadow: black 0px 0px 5px;
}

.answeredQuestion{
  background-color: #fff;
  margin-top: 30px;
  border : 1px solid rgb(28, 18, 82);
  border-radius: 30px;
  padding: 30px;
}

.answeredQuestion form{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  z-index: 1;
}


.answeredQuestion form .inputWrapper{
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}

.contentSubmitButton{
  box-shadow: 0px 0px 10px 0px rgb(20, 12, 64);
  margin-left: 30px;
  border-radius: 10px;
  border : 1px solid rgb(35, 19, 125);
  margin-top: 7px;
  line-height: 1.5;
  font-size: 20px;
  cursor : pointer;
  width: 50px;
  text-align: center;
}

.contentSubmitButton:hover{
  background-color: rgb(178, 168, 230);
}

.response{
  margin-top: 30px;
  margin-left: 30px;
  border : 1px solid rgb(28, 18, 82);
  border-radius: 30px;
  background-color: rgb(235, 232, 250);
  text-align: center;
  color: green;
  transition: 0.3s;
  font-family: Arial, Helvetica, sans-serif;
  padding: 30px;
  box-shadow: 5px 5px 5px 0px rgb(120, 132, 234);
}

.response:hover {
  background-color: rgb(178, 168, 230);
}

.realFormWrapper{
  display: flex !important;
  flex-direction: row !important;
  justify-content: center !important;
  width: 100%;
  z-index: -1;
}

.showFollow{
  background-color: rgb(200, 200, 230);
  margin-top: 30px;
  border-radius: 20px;
  height:20px;
  padding: 5px;
  font-family:fantasy;
  color:grey;
  cursor:pointer;
  font-size: large;
  box-shadow: 0px 2px rgb(20, 12, 64);
}

.showFollow:hover{
  background-color: rgb(180, 160, 200);
}

.unAnsweredWrapper{
  background-color : rgb(70, 60, 120);
  margin-top: 30px;
  padding: 30px;
  border-radius: 30px;
  box-shadow: 0px 7px 10px 0px rgb(20, 12, 64);
}

.unAnsweredWrapper h2{
  color: white;
  transition: 0.3s;
  text-shadow: white 0px 0px 3px;
}




#footer{
  background-color: rgb(70, 60, 120);
  margin-top: 30px;
  padding: 30px;
  border-radius: 30px;
  box-shadow: 0px 7px 10px 0px rgb(20, 12, 64);
}

.checkbutton:hover {
  /* Start the shake animation and make the animation last for 0.5 seconds */
  animation: shake 0.5s !important;

  /* When the animation is finished, start again */
  animation-iteration-count: infinite;
}

@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}