}
.btn:hover{
    display:inline-block;
    text-align: center;
    background-color: lightgreen !important;
}

.btn span{
    display:inline-block;
    position:relative;
    transition: 0.2s;
}

.btn span:before{
    text-align:  center;
    content:'✅';

    position:absolute;
    top:5px;
    left:5px;
    width:0;
    height:100%;
    background:linear-gradient(to right,#061a40,#240046);
    transition: 0.2s;
}

.btn span:after{
    content:"⬆";
    zoom:150%;
    color:black;
    position:absolute;
    position:relative;
    opacity: 0;
    transition: 0.2s;
    top:40px;
}

.btn:hover span:before {
    top:-50px;
    opacity:0;
}

.btn:hover span:after{
    opacity:1;
    top:0;
}